<template>
	<div>
		<el-container>
			<el-aside>
				<el-menu 
        router 
        class="el-menu-vertical-demo"
        unique-opened
        :default-active="activePath"
        active-text-color="#5CC47B"
        >
					<el-submenu :index="item.id+''" v-for="item in menudata" :key="item.name">
						<template slot="title">
							<span>{{item.name}}</span>
						</template>
						<el-menu-item :index="'/'+items.path" 
            v-for="(items, index) in item.children" :key="items.name"
            @click="saveStale(items.path)"
            >
							{{ items.name }}
						</el-menu-item>
					</el-submenu>
				</el-menu>
			</el-aside>
			<el-container>
				<el-main>
             <router-view/>
        </el-main>
			</el-container>
		</el-container>
	</div>
</template>

<script>
export default {
	name: 'Home',
	components: {},
	mixins: [],
	props: {},
	data() {
		return {
			menudata: [
				{
          id:1,
          uid:0,
					name: 'js小功能',
					children: [
						{
              id:2,
              uid:1,
							name: '拖拽',
							path: 'drag',
						},
            	{
              id:3,
              uid:1,
							name: '筋斗云',
							path: 'jindouyun',
						},
            	{
              id:4,
              uid:1,
							name: '书本',
							path: 'Books',
						},
            {
              id:5,
              uid:1,
							name: '选项卡',
							path: 'Tabber',
						},
            {
              id:6,
              uid:1,
							name: '和电脑猜拳',
							path: 'Guess',
						},
             {
              id:7,
              uid:1,
							name: '轮播图',
							path: 'Swiper',
						},
					],
				},
        {
           id:30,
          uid:0,
					name: 'css',
          	children: [
              {
                id:31,
                uid:30,
                name: '形状',
                path: 'Shape',
						  },
              {
                id:32,
                uid:30,
                name: '居中',
                path: 'Center',
						  },
              {
                id:33,
                uid:30,
                name: '三栏布局',
                path: 'sanlan',
						  },
               {
                id:34,
                uid:30,
                name: '放大',
                path: 'Scale',
						  },
               {
                id:35,
                uid:30,
                name: '3d导航栏',
                path: 'Nav',
						  },
            ]
        }
			],
      activePath:''
		}
	},
	computed: {},
	watch: {},
	created() {
    	this.activePath = window.sessionStorage.getItem('activePath')
      if(this.activePath==null) {
        console.log(this.activePath);
        this.activePath = '/drag'
      }
  },
	mounted() {},
	destroyed() {},
	methods: {
    saveStale(path) {
      window.sessionStorage.setItem('activePath','/'+path)
    }
  },
}
</script>
<style lang='less' scoped>
  .el-aside {
    position: fixed;
    top:0;
    left:0;
  }
  .el-main {
    position: relative;
    margin-left: 300px;
    min-height: 500px;
  }
</style>
